/*------------------------------------*\
:    Parkers Normalize - Layout
\*------------------------------------*/
html,
body {
  height: 100%; }

html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

/*------------------------------------*\
:    Parkers Normalize - Forms
\*------------------------------------*/
button,
input,
select {
  background: none;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 0;
  height: 40px;
  padding: 0 10px;
  appearance: none; }
  button:focus,
  input:focus,
  select:focus {
    outline: 0; }

button {
  border: 0; }

textarea {
  width: 100%; }

input[type="radio"] {
  display: none; }
  input[type="radio"] + label {
    padding-left: 30px;
    position: relative; }
    input[type="radio"] + label:before {
      content: '';
      display: block;
      width: 20px;
      height: 20px;
      background: #ccc;
      position: absolute;
      left: 0;
      border-radius: 100%; }
  input[type="radio"]:checked + label:before {
    background: #000; }

input[type="checkbox"] {
  appearance: checkbox;
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  margin-right: 10px;
  cursor: pointer; }

/*------------------------------------*\
:    Parkers Normalize - Type
\*------------------------------------*/
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  font-weight: normal; }

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0; }
